body {
  margin: 0;
  font-family: 'Inter', system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #d8e0eb;
}

* {
  font-family: 'Inter', system-ui;
}

.wrapper {  
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #eaeaea;
  h3,
  p {
    margin: 0;
  }
}

.content {
  h1 {
    margin:0;
  }
}

.button {
  background: #FFFFFF;
  border: 1px solid #F2F2F2;
  box-sizing: border-box;
  border-radius: 8px;
  height: 32px;
  width: 32px;
  cursor: pointer;
}

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0px 15px;
  position: relative;
  .clear {
    position: absolute;
    right: 15px;
    width: 18px;
    height: 18px;
    top: 13px;
  }
  input {
    border:0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }
}


.items {
  flex: 1;
}

.cartTotalBlock {
  ul {
    display:block;
    margin-bottom: 40px !important;
    li {
      display:flex;
      align-items: flex-end;
      margin-bottom: 20px;
    }
    div {
      flex: 1;
      height:1px;
      border-bottom: 1px dashed #dfdfdf;
      position: relative;
      top: -4px;
      margin: 0 7px;
    }
    
  }
  
  .greenButton {
    position: relative;
    
    &:disabled {
      animation: button-loading 0.8s ease-in-out infinite;
    }
    
    &:hover {
      img {
        transform:translateX(5px)
      }
    }
    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out
    }
  }
  
}

.greenButton {
  width: 100%;
  height: 55px;
  background: #9DD558;
  border-radius: 18px;
  border: 0;
  color:white;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.1s ease-in-out;

  &:disabled {
    background-color: #bebebe !important;
    cursor: default;
  }

  &:hover {
    background: lighten(#9DD558, 5%);
  }

  &:active {
    background: darken(#9DD558, 5%);
  }
}

.cartItem {
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow:hidden;
  padding:15px;

  .cartItemImg {
    height:70px;
    width:70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin-right: 20px;
  }
  p {
    font-size: 16px;
    margin: 0;
  }
  b {
    font-size: 14px;
  }

  .removeBtn {
    opacity:0.5;
    cursor: pointer;
    transition: opacity 0.15s ease-in-out;
    &:hover {
      opacity:1;
    }
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

.cartEmpty {
  text-align: center;
  p {
    width: 280px;
    line-height: 24px;
  }
  .greenButton {
    width: 245px;
    margin-top: 20px;
    &:hover {
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }
  }
  
  img {
    position:relative;
    top: 1px;
    // transform: rotate(180deg);
    margin-right: 15px;
    transition: transform 0.15s ease-in-out;
  }
}